import ICON from './icon';

class ContentFooter extends HTMLElement {
    connectedCallback() {
        this.render();
    }

    render() {
        this.innerHTML = `
        <ul>
            <li class="flx-column-center">
                <img class="logo-footer" src="${ICON._logo}" alt="kuliner nusantaraku">
                <q class="txt-slogan">banyak rasa, penuh cinta, kenangan tercipta</q>
            </li>
        </ul>

        <ul>
            <li class="tf-uppercase">tentang kami</li>
            <li><a href="#">Kisah kami</a></li>
            <li><a href="#">Buletin</a></li>
            <li><a href="#">FAQs</a></li>
        </ul>

        <ul>
            <li class="tf-uppercase">Kuliner</li>
            <li><a href="#/explore">Cari Kuliner</a></li>
            <li><a href="#/partners">Mitra Kami</a></li>
        </ul>

        <ul>
            <li class="tf-uppercase">dukungan</li>
            <li><a href="#">Syarat & Ketentuan</a></li>
            <li><a href="#">Kebijakan Privasi</a></li>
        </ul>

        <ul>
            <!-- social media -->
            <li class="tf-uppercase">ikuti kami</li>
            <li class="social-media">
                <a href="https://www.instagram.com/mfathurrohman97/" aria-label="follow instagram">
                    <img src="${ICON._instagram}" alt="instagram">
                </a>
                <a href="https://www.facebook.com/mfathurrohmanm" aria-label="follow facebook">
                    <img src="${ICON._facebook}" class="ml-1" alt="facebook">
                </a>
                <a href="#" aria-label="follow twitter">
                    <img src="${ICON._twitter}" class="ml-1" alt="twitter">
                </a>
                <a href="https://www.linkedin.com/in/mfathurrohmanmauludin/" aria-label="follow linkedIn">
                    <img src="${ICON._linkedIn}" class="ml-1" alt="linkedin">
                </a>
            </li>

            <li>
                <div class="download-apps">
                    <a href="#" aria-label="get it on google play">
                        <img src="${ICON._play}" alt="play store">
                    </a>
                    <a href="#" aria-label="download on the app store">
                        <img src="${ICON._apple}" alt="app store">
                    </a>
                </div>
            </li>
        </ul>
        `;
    }
}

customElements.define('content-footer', ContentFooter);
