const { default: ICON } = require('./icon');

class HeroComponent extends HTMLElement {
    connectedCallback() {
        this.render();
    }

    render() {
        this.innerHTML = `
                <div class="hero-new">
                    <div class="hero-new-left">
                        <h1 class="tf-uppercase" style="font-size: 30pt; letter-spacing: 6px" tabindex="0">kuliner nenek moyang yang melegenda</h1>
                        <p style="margin: 14px 0; max-width: 400%; width: 100%;" tabindex="0">
                            Dengan bahan alam yang penuh dengan khasiat dan keunikan rasa, kuliner nusantara menyajikan suatu cita rasa
                            yang menyentuh hati anda.
                        </p>
                        <button class="btn-explore tf-uppercase txt-center" onclick="window.location.href='#/explore'">
                            Ayo Jelajahi
                        </button>
                        <div class="advantage">
                            <div class="advantage-icon" style="display: flex; align-items: center;" tabindex="0">
                                <img src="./${ICON._leaf}" alt="bahan alam" width="40">
                                <span style="margin-left: 0.8em;">100% bahan alam</span>
                            </div>
                            <div class="advantage-icon" style="display: flex; align-items: center;" tabindex="0">
                                <img src="./${ICON._flavoring}" alt="keunikan rasa" width="40">
                                <span style="margin-left: 0.8em;">100% keunikan rasa</span>
                            </div>
                            <div class="advantage-icon" style="display: flex; align-items: center;" tabindex="0">
                                <img src="./${ICON._flavor}" alt="tidak ada pengawet" width="40">
                                <span style="margin-left: 0.8em;">0% pengawet buatan</span>
                            </div>
                        </div>
                    </div>
                    <div class="hero-new-right">
                        <picture>
                            <source media="(max-width: 400px)" srcset="./img/hero/hero-image-small.jpg">
                            <img style="width: 100%;
                            max-width: 460px;
                            height: 100%;
                            border-radius: 30px;" src="./img/hero/hero-image.jpg" alt="hero-image">
                        </picture>
                    </div>
                </div>`;
    }
}

customElements.define('hero-section', HeroComponent);
