import './reviewers.js';
import sliderReviewers from '../utils/slider-initiator.js';

class ReviewersComponent extends HTMLElement {
  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML = `
        <!-- reviewers section -->
        <section class="flx-column-center bg-comment" id="/Reviewers">
          <div class="title" id="myReviewers">Apa pendapat mereka?</div>
          <div class="scroll-horizontal-reviews">
             <content-reviewers class="d-webkit-inline-box"></content-reviewers>
            </div>
          </div>
          <div style="text-align: right; margin-top: 1.5em;">
            <a class="btn-see-all" href="#/our-reviewers">Lihat Semuanya</a>
          </div>
        </section>
       `;

    const slider = document.querySelector('.scroll-horizontal-reviews');
    sliderReviewers(slider);
  }
}

customElements.define('reviewers-section', ReviewersComponent);
