import './partners.js';

class PartnerComponent extends HTMLElement {
    connectedCallback() {
        this.render();
    }

    render() {
        this.innerHTML = `
        <!-- partners section -->
        <section class="bg-partners flx-column-center" id="/partners">
          <div class="title" id="myPartners">Mitra Kami</div>
          <content-partners></content-partners>
        </section>
       `;
    }
}

customElements.define('partner-section', PartnerComponent);
