import ICON from './icon';

class AppBar extends HTMLElement {
    connectedCallback() {
        this.render();
    }

    render() {
        this.innerHTML = `
            <div class="logo">
                <a href="/" aria-label="kuliner nusantaraku">
                    <img src="${ICON._logo}" alt="kuliner nusantaraku">
                </a>
            </div>

            <div class="drawer-menu">
                <button type="menu" id="nav-drawer" title="navigation button" aria-label="navigation drawer">
                    <img class="icon-nav" src="${ICON._menu}" alt="navigation">
                </button>
            </div>

            <nav id="menu">
                <ul id="nav-top">
                    <li><a href="#/home">beranda</a></li>
                    <li><a href="#/my-favorite">favoritku</a></li>
                    <li><a href="#/our-reviewers">ulasan</a></li>
                    <li><a href="#/about">tentang kami</a></li>
                </ul>
            </nav>
        `;
    }
}

customElements.define('app-bar', AppBar);
