/* eslint-disable array-callback-return */
class partnersItem extends HTMLElement {
    connectedCallback() {
        this.render();
    }

    render() {
        const getSelector = document.querySelector('content-reviewers');
        fetch('./data/data-masakan-nusantara.json')
            .then((res) => res.json())
            .then((data) => {
                data.reviewerAplikasi.slice(0, 5).map((read) => {
                    getSelector.insertAdjacentHTML('beforeend', `
                    <div class="card-reviewer">
                        <div class="" style="display: flex; justify-content: flex-start;">
                            <img src=${read.imageUrl} 
                                style="
                                border-radius: 100%;
                                border: 1px solid #fa63a0;
                                background-size: cover;
                                object-fit: cover;
                                object-position: top;
                                max-height: 60px;
                                max-width: 60px;
                                width: 100%;
                                height: 100%;                            
                                "
                            alt="user-photo">
                            <div class="" style="display: flex; flex-direction: column; margin-left: 1em; width: 100%;">
                                <div class="" style="display: flex; justify-content: space-between; align-items: center;">
                                    <div class="" style="text-transform: capitalize;">${read.namaReviewer}</div>
                                    <div class="" style="font-size: x-small;">${read.tanggal}</div>
                                </div>
                                
                                <div class="" style="display: flex; justify-content: space-between; align-items: center;">
                                    <div class="" style="display: flex; gap: 6px;">
                                        <img src="./icon/star.svg" alt="star" width="15">
                                        <img src="./icon/star.svg" alt="star" width="15">
                                        <img src="./icon/star.svg" alt="star" width="15">
                                        <img src="./icon/star.svg" alt="star" width="15">
                                        <img src="./icon/star.svg" alt="star" width="15">
                                    </div>
                                    <div class="" style="font-size: x-small; text-transform: capitalize;" aria-label="usia pengguna">${read.umur} tahun</div>
                                </div>
                            </div>
                        </div>
                        <div style="font-size: small; width: 100%; white-space: normal; margin: 1em 0;">
                            ${read.komentar}
                        </div>
                        <div class="tf-capitalize" style="position: absolute; bottom: 5px; font-size: small; width: 274px; text-align: center;">
                            - ${read.pekerjaan} -
                        </div>
                    </div>`);
                });
            });
    }
}

customElements.define('content-reviewers', partnersItem);
